<template>
  <div>
    <div class="m-container">
      <view-header title="排班管理"></view-header>
      <div style="width: 100%" class="flex flex-row flex-start">
        <p style="margin-right: 20px">会员:</p>
        <el-input v-model="input" style="width: 200px" placeholder="用户ID/手机号/微信号"></el-input>
        <button class="btn">搜索</button>
      </div>
      <div class="m-table-div">
        <table>
          <thead>
            <th>会员姓名</th>
            <th>手机号</th>
            <th>微信号</th>
            <th>积分</th>
            <th>最近增加积分时间</th>
            <th>操作</th>
          </thead>
          <tbody>
            <tr v-for="item in 10" :key="item">
              <td>朱自清</td>
              <td>13811111111</td>
              <td>11111</td>
              <td>300</td>
              <td>20</td>
              <td>
                <p class="pointer tablebtn" @click="dialogVisible1 = true">详情</p>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div style="width: 100%; margin: 20px 0" class="flex flex-row flex-end">
        <el-pagination
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400">
        </el-pagination>
      </div>
    </div>
    <el-dialog
      title="积分详情"
      :visible.sync="dialogVisible1"
      width="80%">
      <p class="header">会员信息</p>
      <div class="m-table-div">
        <table>
          <thead>
            <th>会员</th>
            <th>联系电话</th>
            <th>微信号</th>
            <th>性别</th>
            <th>出生年月</th>
            <th>注册时间</th>
          </thead>
          <tbody>
            <tr>
              <td>张三</td>
              <td>1388888888</td>
              <td>wexdd225</td>
              <td>男</td>
              <td>1987-6（31岁）</td>
              <td>2017-06-05  10:35:52</td>
            </tr>
          </tbody>
        </table>
      </div>
      <p class="header">积分记录</p>
      <div class="shaixuan flex flex-row flex-start">
        <p style="margin-right: 10px">结算时间</p>
        <el-date-picker
          v-model="value1"
          type="daterange"
          align="right"
          unlink-panels
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          :picker-options="pickerOptions2">
        </el-date-picker>
      </div>
      <div class="m-table-div">
        <table>
          <thead>
            <th>结算时间</th>
            <th>交易号</th>
            <th>单号</th>
            <th>支付方式</th>
            <th>金额（元）</th>
            <th>增加积分</th>
            <th>截止结算日期合计</th>
          </thead>
          <tbody>
            <tr v-for="item in 10" :key="item">
              <td>2017-07-12  08:23:56</td>
              <td>112233445678998</td>
              <td>12345678907</td>
              <td>微信</td>
              <td>23.00</td>
              <td class="red">23</td>
              <td class="red">2030</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div style="width: 100%; margin: 20px 0" class="flex flex-row flex-end">
        <el-pagination
          :page-sizes="[100, 200, 300, 400]"
          :page-size="100"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400">
        </el-pagination>
      </div>
    </el-dialog>
  </div>
</template>
<script type="text/ecmascript-6">
import ViewHeader from 'base/view-header/view-header'
export default {
  components: {
    ViewHeader
  },
  data () {
    return {
      input: '',
      dialogVisible1: false,
      value1: '',
      pickerOptions2: {
        shortcuts: [{
          text: '最近一周',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近一个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近三个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }]
      }
    }
  }
}
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
@import "~common/stylus/base"
@import "~common/stylus/variable"
.m-container
  @extend .flex
  @extend .flex-column
  width: 100%
  height: 100%
  padding: 0 10px
.green
  color: $color-back-green_0
.red
  color: $color-back-red_0
.btn
  border: 0px
  background-color: $color-back-blue_0
  color: $color-back-white_0
  font-weight: bolder
  padding: 0 15px
  margin-left: 10px
  height: 40px
  font-size: $size-16
.tablebtn
  font-size: $size-14
  color: $color-back-green_0
  font-weight: bold
.header
  margin: 20px 0
  font-size: $size-16
  color: $color-back-black_0
.shaixuan
  border: 1px solid $color-back-gray_2
  padding: 20px
</style>


